<template>
  <div id="sign-up-main-wrapper" class="singUpMainWrapper">
    <h3>Za konto</h3>
    <b-form @submit="onSubmit" @reset="onReset" v-if="show">
      <b-form-group
        id="loginFieldset"
        description="np. jan12"
        label="Podaj login"
        label-for="loginInput"
        :invalid-feedback="loginInvalidFeedback"
        :state="loginState">
        <b-form-input id="loginInput" :state="loginState" v-model.trim="form.login" required></b-form-input>
      </b-form-group>

      <b-form-group
        id="emailFieldset"
        description="np. jankowalski@gmail.com"
        label="Podaj e-mail"
        label-for="emailInput"
        :invalid-feedback="emailInvalidFeedback"
        :state="emailState">
        <b-form-input id="emailInput" :state="emailState" v-model.trim="form.email" required></b-form-input>
      </b-form-group>

      <b-form-group
        id="passwordFieldset"
        description="Uyj co najmniej 8 znakw. Nie uywaj hasa z innych serwisw."
        label="Podaj haso"
        label-for="passwordInput"
        :invalid-feedback="passwordInvalidFeedback"
        :state="passwordState">
        <b-form-input type="password" id="passwordInput" :state="passwordState"
                      v-model.trim="form.password" required></b-form-input>
      </b-form-group>

      <b-form-group
        id="nameFieldset"
        description="(To pole nie jest wymagane)."
        label="Podaj imi"
        label-for="nameInput"
        :invalid-feedback="nameInvalidFeedback"
        :state="nameState">
        <b-form-input id="nameInput" :state="nameState" v-model.trim="form.name"></b-form-input>
      </b-form-group>

      <b-form-group
        id="surnameFieldset"
        description="(To pole nie jest wymagane)."
        label="Podaj nazwisko"
        label-for="surnameInput"
        :invalid-feedback="surnameInvalidFeedback"
        :state="surnameState">
        <b-form-input id="surnameInput" :state="surnameState" v-model.trim="form.surname"></b-form-input>
      </b-form-group>

      <b-form-group
        id="townFieldset"
        description="(To pole nie jest wymagane)."
        label="Podaj miejscowo"
        label-for="townInput"
        :invalid-feedback="townInvalidFeedback"
        :state="townState">
        <b-form-input id="townInput" :state="townState" v-model.trim="form.town"></b-form-input>
      </b-form-group>

      <b-button type="submit" variant="primary">Zaloguj</b-button>
      <b-button type="reset" variant="danger">Wyczy formularz</b-button>
    </b-form>
  </div>
</template>

<script>
  export default {
    name: "SignUp",
    data() {
      return {
        form: {
          login: '',
          email: '',
          password: '',
          name: '',
          surname: '',
          town: ''
        },
        show: true,
        errors: []
      }
    },
    computed: {
      loginState() {
        return this.form.login.length >= 4 && this.form.login.length <= 30 ? true : false;
      },
      loginInvalidFeedback() {
        if (this.form.login.length >= 4 && this.form.login.length <= 30) {
          return '';
        } else if (this.form.login.length > 0 || this.form.login.length > 30) {
          return 'Login musi skada si z 4 do 30 znakw.';
        } else {
          return 'Login musi skada si z 4 do 30 znakw.';
        }
      },
      emailState() {
        const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(this.form.email);
      },
      emailInvalidFeedback() {
        if (this.emailState === true && this.form.email.length <= 254) {
          return '';
        } else if (this.form.email.length > 0 && this.emailState === false) {
          return 'E-mail nieprawidowy.';
        } else if (this.form.email.length > 254) {
          return 'Podany e-mail jest zbyt dugi.';
        } else {
          return 'Pole e-mail nie moe by puste.';
        }
      },
      passwordState() {
        return this.form.password.length >= 8 && this.form.password.length <= 60 ? true : false;
      },
      passwordInvalidFeedback() {
        if (this.form.password.length >= 8 && this.form.password.length <= 60) {
          return '';
        } else if (this.form.password.length > 0 || this.form.password.length > 60) {
          return 'Haso musi skada si z 8 do 60 znakw.';
        } else {
          return 'Haso musi skada si z 8 do 60 znakw.';
        }
      },
      nameState() {
        return this.form.name.length <= 30 ? true : false;
      },
      nameInvalidFeedback() {
        if (this.form.name.length <= 30) {
          return '';
        } else {
          return 'To pole moe zawiera maksymalnie 30 znakw.';
        }
      },
      surnameState() {
        return this.form.surname.length <= 30 ? true : false;
      },
      surnameInvalidFeedback() {
        if (this.form.surname.length <= 30) {
          return '';
        } else {
          return 'To pole moe zawiera maksymalnie 30 znakw.';
        }
      },
      townState() {
        return this.form.town.length <= 30 ? true : false;
      },
      townInvalidFeedback() {
        if (this.form.town.length <= 30) {
          return '';
        } else {
          return 'To pole moe zawiera maksymalnie 30 znakw.';
        }
      },
      // loginValidFeedback() {
      //   return this.loginState === true ? 'Thank you' : ''
      // }
    },
    methods: {
      onSubmit(evt) {
        evt.preventDefault();
        this.errors = [];
        if (!this.loginState) {
          this.errors.push('Bdny login.');
        }
        if (!this.emailState) {
          this.errors.push('Bdny email.');
        }
        if (!this.passwordState) {
          this.errors.push('Bdne haso.');
        }
        if (!this.nameState) {
          this.errors.push('Bdne imi.');
        }
        if (!this.surnameState) {
          this.errors.push('Bdne nazwisko.');
        }
        if (!this.townState) {
          this.errors.push('Bdna miejscowo.');
        }
        if (this.errors.length == 0) {
          return true;
          alert(JSON.stringify(this.form));
        }
      },
      onReset(evt) {
        evt.preventDefault();
        this.form.login = '';
        this.form.email = '';
        this.form.password = '';
        this.form.name = '';
        this.form.surname = '';
        this.form.town = '';
        this.show = false;
        this.$nextTick(() => {
          this.show = true
        });
      }
    },


  }
</script>

<style lang="scss" scoped>
  .singUpMainWrapper {
    width: 500px;
    margin: 50px auto;

    @media (max-width: 768px) {
      width: 80%;
    }
  }

  h3 {
    text-align: center;
  }

</style>



