import Vue from 'vue'
import Router from 'vue-router'
import CinemaHome from '../views/CinemaHome'
import User from '../components/User'
import Reservation from '../components/Reservation'
import Room from '../views/Room'
import Show from '../views/Show'
import NotFound from '../views/NotFound'
import ComingSoon from '../views/ComingSoon'
import MovieDetails from '../views/MovieDetails'
import Login from "../views/Login";
import SignUp from "../views/SignUp";
import AboutUs from "../views/AboutUs"
import Contact from "../views/Contact"
import Home from "../views/Home";
import ManageCinema from "../views/admin/ManageCinema";
import CinemaHomeContent from "../views/CinemaHomeContent";
import CinemaPanel from "../views/admin/CinemaPanel";
import MoviePanel from "../views/admin/MoviePanel";
import RoomPanel from "../views/admin/RoomPanel";
import ShowPanel from "../views/admin/ShowPanel";
import AdminPanel from "../views/admin/AdminPanel";
import CinemaPanelEdit from "../views/admin/CinemaPanelEdit";
import MoviePanelEdit from "../views/admin/MoviePanelEdit";
import RoomPanelEdit from "../views/admin/RoomPanelEdit";

Vue.use(Router);

import {store} from '../store/store'

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      beforeEnter: (to, from, next) => {
        if (store.getters.isCinemaSet && to.fullPath === '/') {
          next({name: 'CinemaHome'});
        } else {
          next();
        }
      }
      ,
      children: [
        {
          path: 'cinema',
          name: 'CinemaHome',
          component: CinemaHome,
          beforeEnter: (to, from, next) => {
            if (!store.getters.isCinemaSet) {
              next({name: 'Home'});
            }
            else {
              next();
            }
          }
          ,
          children: [
            {
              path: 'main',
              name: 'CinemaHomeContent',
              component: CinemaHomeContent
            },
            {
              path: 'room/:showId',
              name: 'Room',
              component: Room
            },
            {
              path: 'show',
              name: 'Show',
              component: Show
            },
            {
              path: 'coming-soon',
              name: 'ComingSoon',
              component: ComingSoon
            },
            {
              path: 'movie/:movieId',
              name: 'Movie',
              component: MovieDetails,
            },
            {
              path: 'login',
              name: 'Login',
              component: Login,
            },
            {
              path: 'sign-up',
              name: 'SignUp',
              component: SignUp,
            },
            {
              path: 'about-us',
              name: 'AboutUs',
              component: AboutUs,
            },
            {
              path: 'contact',
              name: 'Contact',
              component: Contact,
            },
          ]
        },
        {
          path: 'admin-panel',
          name: 'AdminPanel',
          component: AdminPanel,
          children: [
            {
              path: 'manage-armchairs',
              name: 'ManageArmchairs',
              component: ManageCinema,
            },
            {
              path: 'manage-cinema',
              name: 'CinemaPanel',
              component: CinemaPanel,
            },
            {
              path: 'manage-movie',
              name: 'MoviePanel',
              component: MoviePanel,
            },
            {
              path: 'manage-room',
              name: 'RoomPanel',
              component: RoomPanel,
            },
            {
              path: 'manage-cinema-edit',
              name: 'CinemaPanelEdit',
              component: CinemaPanelEdit,
            },
            {
              path: 'manage-movie-edit',
              name: 'MoviePanelEdit',
              component: MoviePanelEdit,
            },
            {
              path: 'manage-room-edit',
              name: 'RoomPanelEdit',
              component: RoomPanelEdit,
            },
            {
              path: 'show-manage',
              name: 'ShowPanel',
              component: ShowPanel,
            },
          ]
        },
      ]
    },
    {
      path: '/user',
      name: 'User',
      component: User
    },
    {
      path: '/reservation',
      name: 'Reservation',
      component: Reservation
    },
    {
      path: '/404',
      name: '404',
      component: NotFound
    },
    {
      path: '*',
      name: 'NotFound',
      redirect: '404'
    }
  ]

})



