<template>

  <div id="coming-soon-entry-wrapper">
    <img id="cover" :src="movie.urlImageLink"/>
    <p id="title">{{ movie.title }}</p>
    <p id="date">W kinach od {{ releaseDate }}</p>
  </div>

</template>

<script>
  export default {
    name: "ComingSoonEntry",
    props: {
      movie: Object,
    },
    computed: {
      releaseDate: function () {
        let date = new Date(this.movie.sinceDate);
        return this.$options.filters.leadingZero(date.getDate()) + '.' + this.$options.filters.leadingZero(parseInt(date.getMonth() + 1)) + '.' + date.getFullYear();
      }
    },
    filters: {
      leadingZero: function (value) {
        if (!value) return '';
        value = '0' + value.toString();
        return value.slice(-2);
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../scss/colors";

  $content-width: 203px;
  $padding: 10px;

  #coming-soon-entry-wrapper {
    /*<!--width: $content-width + 2*$padding + 2px;-->*/
    text-align: center;
    padding: $padding;
    margin: 10px;
    border-radius: 6px;
    border: solid 1px $primary-light;
    background-color: $background-additional;
    transition: 0.2s;
    &:hover {
      outline: 0;
      border-color: $secondary;
      box-shadow: 0 0 10px $secondary;
    }
  }

  #cover {
    width: $content-width;
    text-align: center;
    height: 300px;
  }

  #title {
    max-width: $content-width;
    margin: 5px auto;
    font-weight: bold;

  }

  #date {
    max-width: $content-width;
    margin: auto;
  }

</style>



