<template>
  <div class="manage-cinema-content-wrapper">
    <room-add-armchairs></room-add-armchairs>
  </div>
</template>

<script>
  import RoomAddArmchairs from "../../components/admin/RoomAddArmchairs";
  export default {
    name: "ManageCinema",
    components: {RoomAddArmchairs},
    data() {
      return {
        cinemaList: [{value: null, text: '-- Wybierz kino --'}],
        selectedCinema: null,
      }
    },
    created() {
      this.$http.get(`/api/getCinema`)
        .then(response => {
          response.data.data.map(cinema => {
            this.cinemaList.push({value: cinema, text: `${cinema.town}, ${cinema.street} ${cinema.building_number}`})
          })
        }).catch(error => {
      });
    },
    methods: {
      activate(cinema) {
        this.selectedCinema = cinema;
      }
    }
  }
</script>

<style lang="scss" scoped>

</style>



