<template>
  <div class="wrapper">
    <div class="spinner" v-if="isReady === false">
      <half-circle-spinner
        :animation-duration="1000"
        :size="80"
        color="#ffc107"/>
    </div>

    <div v-else :style="styleRoomBorder">
      <div class="screen">EKRAN</div>
      <div v-for="item in seats" :class="[{chosenSeat: item.isChosen}, item.class]">
        <div
          v-if="typeof item.seat !== 'undefined' && typeof item.seat.seat_id !== 'undefined'"
          @click="bookSeat(item)"
          class="seatNumber">{{item.seat.seat_number}}
          <div
            v-if="item.seat.seat_number == 1"
            class="rowLabel">
            {{item.seat.row}}
          </div>
        </div>
      </div>
    </div>

    <b-container>
      <b-row>
        <b-col class="seatDescription">
          <b-row>
            <b-col md="6" lg="7">
              <div>
                <div class="seat available" id="available"></div>
                <div class="label">Wolne miejsce</div>
              </div>
            </b-col>
            <b-col md="6" lg="5">
              <div>
                <div class="seat vip" id="vip"></div>
                <div class="label">Wolne miejsce VIP</div>
              </div>
            </b-col>
          </b-row>
          <b-row>
            <b-col md="6" lg="7">
              <div>
                <div class="seat disabled" id="disabled"></div>
                <div class="label">Wolne miejsce dla niepenosprawnych</div>
              </div>
            </b-col>
            <b-col md="6" lg="5">
              <div>
                <div class="seat chosenSeat" id="chosen"></div>
                <div class="label">Wybrane miejsce</div>
              </div>
            </b-col>
          </b-row>
          <b-row>
            <b-col md="10" lg="10">
              <div class="seat unavailable" id="unavailable"></div>
              <div class="label">Zajte miejsce</div>
            </b-col>
          </b-row>
        </b-col>
      </b-row>

    </b-container>
  </div>
</template>

<script>
  import HalfCircleSpinner from "epic-spinners/src/components/lib/HalfCircleSpinner";

  export default {
    name: "ScreeningRoom",
    components: {HalfCircleSpinner},
    data: function () {
      return {
        styleRoomBorder: {
          border: 'solid 1px rgba(0, 0, 0, .2)',
          cursor: 'crosshair',
          paddingLeft: '20px',
          paddingRight: '20px',
        },
        showId: null,
        seats: [],
        rawSeats: [],
        chosenSeats: [],
        isReady: false
      };
    },
    created() {
      this.getSeats();
    },
    methods: {
      bookSeat(item) {
        if (!item.class.includes('unavailable')) {
          item.isChosen = !item.isChosen;
          if (item.isChosen) {
            this.chosenSeats.push(item.seat.seat_id);
          } else {
            let index = this.chosenSeats.indexOf(item.seat.seat_id);
            if (index !== -1) {
              this.chosenSeats.splice(index, 1);
            }
          }
        }
      },
      getSeats() {
        this.$http.get(`/api/getSeatByShowId/${this.$route.params.showId}`)
          .then(response => {
              this.rawSeats = response.data.data.map(function (item) {
                return {
                  seat_id: item.id,
                  status: item.status,
                  x_cord: item.armchair.coordinates_X,
                  y_cord: item.armchair.coordinates_Y,
                  row: item.armchair.row,
                  seat_number: item.armchair.seat_number,
                  armchair_type: item.armchair.armchair_type
                };
              });
              this.prepareSeats();
            }
          )
      },
      prepareSeats() {
        let seatClasses = [];
        for (let i = 0; i < 9; i++) {
          for (let j = 0; j < 20; j++) {
            if (j === 0) {
              seatClasses.push({class: 'blank rowStart', x: j, y: i, isChosen: false});
            } else {
              seatClasses.push({class: 'blank', x: j, y: i, isChosen: false});
            }
          }
        }
        for (let i = 0; i < this.rawSeats.length; i++) {
          for (let j = 0; j < seatClasses.length; j++) {
            if (this.rawSeats[i].x_cord === seatClasses[j].x && this.rawSeats[i].y_cord === seatClasses[j].y) {
              this.changeSeatClass(seatClasses, j, this.rawSeats, i);
            }
          }
        }
        this.seats = seatClasses;
        setTimeout(() => {
          this.isReady = true;
        }, 1000);
      },

      changeSeatClass(seatClassArray, seatIndex, rawSeat, rawSeatIndex) {
        let seatClass = '';
        if (rawSeat[rawSeatIndex].armchair_type === 'VIP' && rawSeat[rawSeatIndex].status === 'wolne') {
          seatClass = 'seat vip'
        } else if (rawSeat[rawSeatIndex].armchair_type === 'specjalne' && rawSeat[rawSeatIndex].status === 'wolne') {
          seatClass = 'seat disabled'
        } else if (rawSeat[rawSeatIndex].status === 'wolne') {
          seatClass = 'seat available'
        } else {
          seatClass = 'seat unavailable'
        }
        seatClassArray[seatIndex].class = seatClassArray[seatIndex].class.replace('blank', seatClass);
        seatClassArray[seatIndex].seat = rawSeat[rawSeatIndex];
      }
    }
  }

</script>

<style lang="scss" scoped>

  .wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-top: 20px;
  }

  .spinner {
    margin-top: 20px;
    margin-bottom: 35px;
  }

  .seat {
    width: 20px;
    height: 20px;
    margin: 2px;
    float: left;

    @media (max-width: 540px) {
      width: 10px;
      height: 10px;
      font-size: 0.5em;
    }

  }

  .blank {
    width: 20px;
    height: 20px;
    margin: 2px;
    float: left;

    @media (max-width: 540px) {
      width: 10px;
      height: 10px;
      font-size: 0.5em;
    }
  }

  .available {
    background-color: #96c131;
  }

  .unavailable {
    background-color: #8b8a91;
  }

  .vip {
    background-color: #9965b5;
  }

  .disabled {
    background-image: linear-gradient(45deg, #96c131 25%, #6a8c1c 25%, #6a8c1c 50%, #96c131 50%, #96c131 75%, #6a8c1c 75%, #6a8c1c 100%);
    background-size: 14.14px 14.14px;
  }

  .available:hover, .vip:hover, .disabled:hover {
    filter: brightness(120%) !important;;
  }

  .chosenSeat {
    background-color: #ff7e00 !important;
    background-image: unset;
  }

  .rowStart {
    clear: both;
  }

  .seatNumber {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    top: -4%;
    left: 2%;
    width: 100%;
    height: 100%;
  }

  .screen {
    display: flex;
    justify-content: center;
    margin: 10px 25px 30px;
    background-color: rgba(57, 130, 168, 0.36);
  }

  .seatDescription {
    margin: 10px 20px 10px;
    padding: 10px;
    border: solid 1px rgba(0, 0, 0, .2);
  }

  .priceList {
    margin: 10px 20px 10px;
    border: solid 1px rgba(0, 0, 0, .2);
  }

  .rowLabel {
    transform: translate(-18px);
    color: #8b8a91;
    position: absolute;
    font-size: 0.7em;
  }

  .label {
    padding-left: 24px;
    margin-left: auto;
    margin-bottom: 0px;
    width: 100%;
  }

  div.col {
    margin: 10px 10px 10px 20px;
    width: 100%;
  }
</style>



