<template>
  <div class="container">

    <h2 style="text-align: center; margin: auto; margin-bottom: 10px; text-transform: uppercase">Panel Administracyjny</h2>
    <b-navbar toggleable="md" type="dark" variant="secondary">
      <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
      <b-collapse is-nav id="nav_collapse">
        <b-navbar-nav fill>
          <b-nav-item-dropdown text="KINO" extra-toggle-classes="nav-link-custom" right>
            <b-dropdown-item :to="{name:'CinemaPanel'}" append>Dodaj</b-dropdown-item>
            <b-dropdown-item :to="{name:'CinemaPanelEdit'}" append>Edytuj</b-dropdown-item>
          </b-nav-item-dropdown>
          <b-nav-item-dropdown text="SALA I FOTELE" extra-toggle-classes="nav-link-custom" right>
            <b-dropdown-item :to="{name:'RoomPanel'}" append>Dodaj sal</b-dropdown-item>
            <b-dropdown-item :to="{name:'RoomPanelEdit'}" append>Edytuj sal</b-dropdown-item>
            <b-dropdown-divider></b-dropdown-divider>
            <b-dropdown-item :to="{name:'ManageArmchairs'}" append>Dodaj fotele</b-dropdown-item>
          </b-nav-item-dropdown>
          <b-nav-item-dropdown text="FILM" extra-toggle-classes="nav-link-custom" right>
            <b-dropdown-item :to="{name:'MoviePanel'}" append>Dodaj film</b-dropdown-item>
            <b-dropdown-item :to="{name:'MoviePanelEdit'}" append>Edytuj film</b-dropdown-item>
          </b-nav-item-dropdown>
          <b-nav-item-dropdown text="SEANSE" extra-toggle-classes="nav-link-custom" right>
            <b-dropdown-item :to="{name:'ShowPanel'}" append>Dodaj seans</b-dropdown-item>
          </b-nav-item-dropdown>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
    <hr>
    <router-view/>
  </div>
</template>

<script>
  export default {
    name: "AdminPanel",
    components: {},
    data() {
      return {
        cinemaId: null
      }
    }
  }
</script>

<style scoped>

</style>



