<template>
<div>
  <div>
    <h2>USER</h2>
    <a @click="getTest()">getTest</a>
    <input v-model="user.name" placeholder="name">
    <input v-model="user.surname" placeholder="surname">
    <input v-model="user.town" placeholder="town">
    <input v-model="user.email" placeholder="email">
    <input v-model="user.login" placeholder="login">
    <input v-model="user.password" placeholder="password">
    <a @click="postTest()">postTest</a>
    <input v-model="userId" placeholder="ID">
    <a @click="deleteTest()">deleteTest</a>
    <tr v-for="us in newUser">
      <td>{{us.id}}</td>
      <td>{{us.name}}</td>
      <td>{{us.surname}}</td>
      <td>{{us.town}}</td>
      <td>{{us.email}}</td>
      <td>{{us.login}}</td>
      <td>{{us.password}}</td>
    </tr>
  </div>
</div>

</template>

<script>
  export default {
    data() {
      return {
        userId: 0,
        newUser: {},
        user: {
        },
       slide: 0,
        sliding: null
      }
    },
    methods: {

      getTest(){
        this.$http.get(`http://localhost:8080/api/getUser`)
        .then(response => {
          this.newUser = response.data.data
        })
      },
      postTest(){
        this.$http.post('http://localhost:8080/api/addOrUpdateUser', this.user).then(function (response) {
          },function (response) {
            });
      },
      deleteTest(){
        this.$http.delete('http://localhost:8080/api/deleteUser', {body: this.userId}).then(function (response) {
          },function (response) {
            });
      }
    }
  }
</script>

<style>
  b-carousel-slide img-src {
    height: 100px;
    width: 100px;
  }
</style>



