<template>
  <div id="show-panel-main-wrapper" class="showPanelMainWrapper">
    <h3>Dodaj repertuar</h3>
    <b-form @submit="onSubmit">
      <b-form-group id="idFieldset"
                    label="Id:"
                    label-for="idInput">
        <b-form-input id="idInput"
                      type="text"
                      v-model="form.id"
                      :disabled="true">
        </b-form-input>
      </b-form-group>
      <b-form-group id="dateFieldset"
                    label="Wpisz dat:"
                    label-for="dateInput">
        <b-form-input id="dateInput"
                      type="date"
                      v-model="form.date"
                      required
                      placeholder="Wpisz date">
        </b-form-input>
      </b-form-group>

      <b-form-group id="timeFieldset"
                    label="Wpisz godzin:"
                    label-for="timeInput">
        <b-form-input id="timeInput"
                      type="time"
                      v-model="time"
                      required
                      placeholder="Wpisz godzin">
        </b-form-input>
      </b-form-group>

      <p>Wybierz sal:</p>
      <div class="sizeOfBList">
        <b-list-group v-for="(item, index) of room" :key="room.id">
          <b-list-group-item v-bind:class="{ 'active' : isSelectedRoom(index) }" v-on:click="selectedRoom = index" @click="setRoom(item)">Numer sali:{{item.roomNumber}} Miejscowo: {{item.cinema.town}}
          </b-list-group-item>
        </b-list-group>
      </div>
      <p>Wybierz film:</p>
      <div class="sizeOfBList">
        <b-list-group v-for="(item, index) of movie" :key="movie.id">
          <b-list-group-item v-bind:class="{ 'active' : isSelectedMovie(index) }" v-on:click="selectedMovie = index" @click="setMovie(item)">{{item.title}}</b-list-group-item>
        </b-list-group>
      </div>

      <p>Wybierz projekcj:</p>
      <b-form-select id="projectionInput"
                     :options="projection"
                     required
                     v-model="form.projection">
      </b-form-select>

      <p>Wybierz wersj:</p>
      <b-form-select id="versionInput"
                     :options="version"
                     required
                     v-model="form.version">
      </b-form-select>
      <b-button type="submit" variant="primary">Zapisz</b-button>
    </b-form>
  </div>
</template>

<script>
  export default {
    name: "ShowPanel",

    data() {
      return {
        selectedRoom: 0,
        selectedMovie: 0,
        projection: [{value: '2D', text: '2D'}, {value: '3D',text: '3D'}],
        movie: {},
        room: {},
        version: [{value: 'orygina', text: 'orygina'}, {value: 'napisy', text: 'napisy'}, {value: 'lektor', text: 'lektor'}, {value: 'dubbing', text: 'dubbing'} ],
        time: '',
        form: {
          id: null,
          date: '',
          room: {},
          movie: {},
          projection: '',
          version: '',
        },
      }
    },
    methods: {
      onSubmit() {
        this.form.date = this.form.date + "T" + this.time
        this.$http.post('http://localhost:8080/api/addOrUpdateShow', this.form).then(function (response) {
        }, function (response) {
        });
      },
      setRoom(value) {
        this.form.room = value;
      },
      setMovie(value) {
        this.form.movie = value;
      },
      isSelectedMovie(i) {
        return i === this.selectedMovie
      },
      isSelectedRoom(i) {
        return i === this.selectedRoom
      }
    },
    created () {
      const promises = [
        this.$http.get(`/api/getMovie`),
        this.$http.get(`/api/getRoom`),
      ];
      Promise.all(promises)
        .then(response => {
          return response.map(entry => entry.data);
        }).then(data => {
        setTimeout(() => {
          this.movie = data[0].data;
          this.room = data[1].data;
        }, 500);
      });
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../scss/colors";
  .showPanelMainWrapper {
    width: 500px;
    margin: 50px auto;

    @media (max-width: 768px) {
      width: 80%;
    }
  }

  .sizeOfBList {
    max-height: 100px;
    margin-bottom: 10px;
    overflow: auto;

    .list-group-item.active{
      background-color: $primary-light;
      border-color: $primary-light;
    }
  }

</style>



