<template>
  <div id="login-main-wrapper" class="loginMainWrapper">
    <h3>Zaloguj si</h3>
    <b-form @submit="onSubmit" v-if="show">
      <b-form-group id="loginFieldset"
                    label="Login:"
                    label-for="loginInput">
        <b-form-input id="loginInput"
                      type="text"
                      v-model="form.login"
                      required
                      placeholder="Wpisz login">
        </b-form-input>
      </b-form-group>
      <b-form-group id="passwordFieldset"
                    label="Haso:"
                    label-for="passwordInput">
        <b-form-input id="passwordInput"
                      type="password"
                      v-model="form.password"
                      required
                      placeholder="Wpisz haso">
        </b-form-input>
      </b-form-group>

      <b-button type="submit" variant="primary">Zaloguj</b-button>
    </b-form>
  </div>
</template>

<script>
  export default {
    name: "Login",
    data() {
      return {
        form: {
          login: '',
          password: '',
        },
        show: true
      }
    },
    methods: {
      onSubmit(evt) {
        evt.preventDefault();
        alert(JSON.stringify(this.form));
      },
    },
    computed: {

    }
  }
</script>

<style lang="scss" scoped>
  .loginMainWrapper {
    width: 500px;
    margin: 50px auto;

    @media (max-width: 768px) {
      width: 80%;
    }
  }

  h3 {
    text-align: center;
  }

</style>



