<template>
  <div id="movie-panel-main-wrapper" class="moviePanelMainWrapper">
    <h3>Dodaj film</h3>
    <b-form @submit="onSubmit">
      <b-form-group id="idFieldset"
                    label-for="idInput">
        <b-form-input id="idInput"
                      type="text"
                      :disabled="true"
                      v-model="form.id">
        </b-form-input>
      </b-form-group>

      <b-form-group
        id="titleFieldset"
        label="Podaj tytu"
        label-for="titleInput"
        :invalid-feedback="titleInvalidFeedback"
        :state="titleState">
        <b-form-input id="titleInput" :state="titleState" v-model.trim="form.title" required></b-form-input>
      </b-form-group>

      <b-form-group
        id="durationFieldset"
        label="Podaj dugo trwania filmu"
        label-for="durationInput"
        :invalid-feedback="durationInvalidFeedback"
        :state="durationState">
        <b-form-input type="number" id="durationInput" :state="durationState" v-model.trim="form.duration"
                      required></b-form-input>
      </b-form-group>

      <b-form-group
        id="genreFieldset"
        label="Podaj gatunek"
        label-for="genreInput"
        :invalid-feedback="genreInvalidFeedback"
        :state="genreState">
        <b-form-input id="genreInput" :state="genreState" v-model.trim="form.genre" required></b-form-input>
      </b-form-group>

      <b-form-group
        id="urlImageLinkFieldset"
        label="Podaj link do plakatu"
        label-for="urlImageLinkInput"
        :invalid-feedback="urlImageLinkInvalidFeedback"
        :state="urlImageLinkState">
        <b-form-input id="urlImageLinkInput" :state="urlImageLinkState" v-model.trim="form.urlImageLink"
                      required></b-form-input>
      </b-form-group>

      <b-form-group
        id="originalLanguageFieldset"
        label="Podaj oryginalny jzyk filmu"
        label-for="originalLanguageInput"
        :invalid-feedback="originalLanguageInvalidFeedback"
        :state="originalLanguageState">
        <b-form-input id="originalLanguageInput" :state="originalLanguageState" v-model.trim="form.originalLanguage"
                      required></b-form-input>
      </b-form-group>

      <b-form-group
        id="sinceDateFieldset"
        label="Podaj od kiedy film bdzie dostpny"
        label-for="sinceDateInput">
        <b-form-input type="date" id="sinceDateInput" v-model.trim="form.sinceDate" required></b-form-input>
      </b-form-group>

      <b-form-group
        id="descriptionFieldset"
        label="Podaj opis"
        label-for="descriptionInput"
        :invalid-feedback="descriptionInvalidFeedback"
        :state="descriptionState">
        <b-form-input style="overflow: auto" id="descriptionInput" :state="descriptionState"
                      v-model.trim="form.description" required></b-form-input>
      </b-form-group>

      <b-button type="submit" variant="primary">Zapisz</b-button>
    </b-form>
  </div>
</template>

<script>
  export default {
    name: "MoviePanel",

    data() {
      return {
        form: {
          id: null,
          title: '',
          duration: '',
          genre: '',
          urlImageLink: '',
          originalLanguage: '',
          sinceDate: '',
          description: '',
        },
      }
    },
    computed: {
      titleState() {
        return this.form.title.length >= 1 && this.form.title.length <= 60 ? true : false;
      },
      titleInvalidFeedback() {
        if (this.form.title.length >= 1 && this.form.title.length <= 60) {
          return '';
        } else if (this.form.title.length > 0 || this.form.title.length > 60) {
          return 'Tytu musi skada si z 1 do 60 znakw.';
        } else {
          return 'Tytu musi skada si z 1 do 60 znakw.';
        }
      },
      durationState() {
        return this.form.duration.length >= 1 && this.form.duration.length <= 3 ? true : false;
      },
      durationInvalidFeedback() {
        if (this.form.duration.length >= 1 && this.form.duration.length <= 3) {
          return '';
        } else if (this.form.duration.length > 0 || this.form.duration.length > 3) {
          return 'Czas trwania musi skada si z 1 do 3 znakw.';
        } else {
          return 'Czas trwania musi skada si z 1 do 3 znakw.';
        }
      },
      genreState() {
        return this.form.genre.length >= 1 && this.form.genre.length <= 20 ? true : false;
      },
      genreInvalidFeedback() {
        if (this.form.genre.length >= 1 && this.form.genre.length <= 20) {
          return '';
        } else if (this.form.genre.length > 0 || this.form.genre.length > 20) {
          return 'Gatunek musi skada si z 1 do 20 znakw.';
        } else {
          return 'Gatunek musi skada si z 1 do 20 znakw.';
        }
      },
      urlImageLinkState() {
        return this.form.urlImageLink.length >= 1 && this.form.urlImageLink.length <= 500 ? true : false;
      },
      urlImageLinkInvalidFeedback() {
        if (this.form.urlImageLink.length >= 1 && this.form.urlImageLink.length <= 500) {
          return '';
        } else if (this.form.urlImageLink.length > 0 || this.form.urlImageLink.length > 500) {
          return 'Plakat musi skada si z 1 do 500 znakw.';
        } else {
          return 'Plakat musi skada si z 1 do 500 znakw.';
        }
      },
      originalLanguageState() {
        return this.form.originalLanguage.length >= 1 && this.form.originalLanguage.length <= 30 ? true : false;
      },
      originalLanguageInvalidFeedback() {
        if (this.form.originalLanguage.length >= 1 && this.form.originalLanguage.length <= 30) {
          return '';
        } else if (this.form.originalLanguage.length > 0 || this.form.originalLanguage.length > 30) {
          return 'Jzyk oryginalny musi skada si z 1 do 30 znakw.';
        } else {
          return 'Jzyk oryginalny musi skada si z 1 do 30 znakw.';
        }
      },
      descriptionState() {
        return this.form.description.length >= 1 && this.form.description.length <= 500 ? true : false;
      },
      descriptionInvalidFeedback() {
        if (this.form.description.length >= 1 && this.form.description.length <= 500) {
          return '';
        } else if (this.form.description.length > 0 || this.form.description.length > 500) {
          return 'Opis musi skada si z 1 do 500 znakw.';
        } else {
          return 'Opis musi skada si z 1 do 500 znakw.';
        }
      },
    },
    methods: {
      onSubmit() {
        this.$http.post('http://localhost:8080/api/addOrUpdateMovie', this.form).then(function (response) {
        }, function (response) {
        });
      },
    },
  }
</script>

<style lang="scss" scoped>
  .moviePanelMainWrapper {
    width: 500px;
    margin: 50px auto;

    @media (max-width: 768px) {
      width: 80%;
    }
  }

</style>



