<template>
  <div id="cinema-panel-main-wrapper" class="cinemaPanelMainWrapper">
    <h3>Dodaj kino</h3>
    <b-form @submit="onSubmit">
      <b-form-group id="idFieldset"
                    label-for="idInput">
        <b-form-input id="idInput"
                      type="text"
                      :disabled="true"
                      v-model="form.id">
        </b-form-input>
      </b-form-group>
      <b-form-group
        id="townFieldset"
        label="Podaj miejscowo"
        label-for="townInput"
        :invalid-feedback="townInvalidFeedback"
        :state="townState">
        <b-form-input id="townInput" :state="townState" v-model.trim="form.town" required></b-form-input>
      </b-form-group>

      <b-form-group
        id="streetFieldset"
        label="Podaj ulic"
        label-for="streetInput"
        :invalid-feedback="streetInvalidFeedback"
        :state="streetState">
        <b-form-input id="streetInput" :state="streetState" v-model.trim="form.street" required></b-form-input>
      </b-form-group>

      <b-form-group
        id="buildingNumberFieldset"
        label="Podaj numer budynku"
        label-for="buildingNumberInput"
        :invalid-feedback="buildingNumberInvalidFeedback"
        :state="buildingNumberState">
        <b-form-input id="buildingNumberInput" :state="buildingNumberState" v-model.trim="form.building_number" required></b-form-input>
      </b-form-group>

      <b-form-group
        id="phoneNumberFieldset"
        label="Podaj numer telefonu"
        label-for="phoneNumberInput"
        :invalid-feedback="phoneNumberInvalidFeedback"
        :state="phoneNumberState">
        <b-form-input type="number" id="phoneNumberInput" :state="phoneNumberState" v-model.trim="form.phone_number" required></b-form-input>
      </b-form-group>

      <b-form-group
        id="emailFieldset"
        label="Podaj e-mail"
        label-for="emailInput"
        :invalid-feedback="emailInvalidFeedback"
        :state="emailState">
        <b-form-input id="emailInput" :state="emailState" v-model.trim="form.email" required></b-form-input>
      </b-form-group>

      <b-button type="submit" variant="primary">Zapisz</b-button>
    </b-form>
  </div>
</template>

<script>
  export default {
    name: "CinemaPanel",

    data() {
      return {
        form: {
          id: null,
          town: '',
          street: '',
          building_number: '',
          phone_number: '',
          email: '',
        },
      }
    },
    computed: {
      emailState() {
        const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(this.form.email);
      },
      emailInvalidFeedback() {
        if (this.emailState === true && this.form.email.length <= 254) {
          return '';
        } else if (this.form.email.length > 0 && this.emailState === false) {
          return 'E-mail nieprawidowy.';
        } else if (this.form.email.length > 254) {
          return 'Podany e-mail jest zbyt dugi.';
        } else {
          return 'Pole e-mail nie moe by puste.';
        }
      },
      townState() {
        return this.form.town.length >= 1 && this.form.town.length <= 30 ? true : false;
      },
      townInvalidFeedback() {
        if (this.form.town.length >= 1 && this.form.town.length <= 30) {
          return '';
        } else if (this.form.town.length > 0 || this.form.town.length > 30) {
          return 'Miasto musi skada si z 1 do 30 znakw.';
        } else {
          return 'Miasto musi skada si z 1 do 30 znakw.';
        }
      },
      buildingNumberState() {
        return this.form.building_number.length >= 1 && this.form.building_number.length <= 6 ? true : false;
      },
      buildingNumberInvalidFeedback() {
        if (this.form.building_number.length >= 1 && this.form.building_number.length <= 6) {
          return '';
        } else if (this.form.building_number.length > 0 || this.form.building_number.length > 6) {
          return 'Numer budynku musi skada si z 1 do 6 znakw.';
        } else {
          return 'Numer budynku musi skada si z 1 do 6 znakw.';
        }
      },
      streetState() {
        return this.form.street.length >= 1 && this.form.street.length <= 30 ? true : false;
      },
      streetInvalidFeedback() {
        if (this.form.street.length >= 1 && this.form.street.length <= 30) {
          return '';
        } else if (this.form.street.length > 0 || this.form.street.length > 30) {
          return 'Ulica musi skada si z 1 do 30 znakw.';
        } else {
          return 'Ulica musi skada si z 1 do 30 znakw.';
        }
      },
      phoneNumberState() {
        return this.form.phone_number.length >= 9 && this.form.phone_number.length <= 12 ? true : false;
      },
      phoneNumberInvalidFeedback() {
        if (this.form.phone_number.length >= 9 && this.form.phone_number.length <= 12) {
          return '';
        } else if (this.form.phone_number.length > 0 || this.form.phone_number.length > 12) {
          return 'Numer telefonu musi skada si z 9 do 12 znakw.';
        } else {
          return 'Numer telefonu musi skada si z 9 do 12 znakw.';
        }
      },
    },
    methods: {
      onSubmit() {
        this.$http.post('http://localhost:8080/api/addOrUpdateCinema', this.form).then(function (response) {
        },function (response) {
        });
      },
    }
  }
</script>

<style lang="scss" scoped>
  .cinemaPanelMainWrapper {
    width: 500px;
    margin: 50px auto;

    @media (max-width: 768px) {
      width: 80%;
    }
  }
</style>



