export default {
  filters: {
    showTime: function (value) {
      if (!value) return '';
      return new Date(value).toLocaleTimeString("pl-PL", {hour: '2-digit', minute: '2-digit'});

    },
    showDate: function (value) {
      if (!value) return '';
      return new Date(value).toLocaleDateString("pl-PL", {
        weekday: 'long',
        day: 'numeric',
        month: 'numeric',
        year: 'numeric'
      });
    },
  }
}



