<template>
  <div id="room-panel-main-wrapper" class="roomPanelMainWrapper">
    <h3>Edytuj sal</h3>
    <b-table @row-dblclicked="selectRow" :fields="fields" hover responsive :items="rooms"></b-table>
    <b-form @submit="onSubmit">
      <b-form-group id="idFieldset"
                    label-for="idInput">
        <b-form-input id="idInput"
                      type="text"
                      :disabled="true"
                      v-model="form.id">
        </b-form-input>
      </b-form-group>

      <b-form-group
        id="roomNumberFieldset"
        label="Podaj numer sali"
        label-for="roomNumberInput"
        :invalid-feedback="roomNumberInvalidFeedback"
        :state="roomNumberState">
        <b-form-input type="number" id="roonNumberInput" :state="roomNumberState" v-model.trim="form.roomNumber"
                      required></b-form-input>
      </b-form-group>

      <p>Wybierz kino:</p>
      <div class="sizeOfBList">
        <b-list-group v-for="(item, index) of cinema" :key="cinema.id">
          <b-list-group-item v-bind:class="{ 'active' : isSelectedCinema(index) }" v-on:click="selectedCinema = index"
                             @click="setCinema(item)">Miejscowo: {{item.town}}
          </b-list-group-item>
        </b-list-group>
      </div>

      <b-button type="submit" variant="primary">Zapisz</b-button>
    </b-form>
  </div>
</template>

<script>
  export default {
    name: "RoomPanel",

    data() {
      return {
        fields: ['id', 'roomNumber'],
        selectedCinema: 0,
        cinema: {},
        rooms: [],
        form: {
          id: null,
          roomNumber: '',
          cinema: {},
        },
      }
    },
    computed: {
      roomNumberState() {
        return this.form.roomNumber.length > 0 && this.form.roomNumber.length <= 12 ? true : false;
      },
      roomNumberInvalidFeedback() {
        if (this.form.roomNumber.length >= 1 && this.form.roomNumber.length <= 12) {
          return '';
        } else if (this.form.roomNumber.length > 0 || this.form.roomNumber.length > 12) {
          return 'Numer sali musi skada si z 1 do 12 znakw.';
        } else {
          return 'Numer sali musi skada si z 1 do 12 znakw.';
        }
      },
    },
    methods: {
      onSubmit() {
        this.$http.post('http://localhost:8080/api/addOrUpdateRoom', this.form).then(function (response) {
        }, function (response) {
        });
      },
      setCinema(value) {
        this.form.cinema = value;
        this.$http.get(`/api/getRoom`)
          .then(response => {
            this.rooms = response.data.data.filter(element => {
              if (element != null && element.cinema.id == this.form.cinema.id) {
                return true;
              }
              else return false;
            })
          })
      },
      isSelectedCinema(i) {
        return i === this.selectedCinema
      },
      selectRow(item, index, event) {
        this.form = item;

      }
    },
    created: function () {
      const promises = [
        this.$http.get(`/api/getCinema`),
      ];
      Promise.all(promises)
        .then(response => {
          return response.map(entry => entry.data);
        }).then(data => {
        setTimeout(() => {
          this.cinema = data[0].data;
        }, 500);
      });
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../scss/colors";

  .roomPanelMainWrapper {
    width: 500px;
    margin: 50px auto;

    @media (max-width: 768px) {
      width: 80%;
    }
  }

  .sizeOfBList {
    max-height: 100px;
    margin-bottom: 10px;
    overflow: auto;

    .list-group-item.active {
      background-color: $primary-light;
      border-color: $primary-light;
    }
  }
</style>



